import styles from './InfoBlock.module.scss'
import cooking1 from '../../../assets/images/png/cooking1.png'
import cooking2 from '../../../assets/images/png/cooking2.png'
import pattern from '../../../assets/images/svg/wavy-pattern-big.svg'

export default function InfoBlock() {
    return (
        <div className={styles.infoBlock}>
            <div className={styles.left}>
                <h2 className={styles.title}>     </h2>

                <p className={styles.text}>
                        ,         ? -,   .  
                       ,    ,   . -,    .
                </p>

                <p className={styles.text}>
                           ,          .    
                      ,      ,        .   
                             -.
                </p>

                <p className={styles.text}>
                                 60 !        
                       .
                </p>
            </div>
            <div className={styles.right}>
                <div className={styles.container}>
                    <img className={styles.front} src={cooking1} alt="cookech1.svg"/>
                    <div className={styles.back}>
                        <img className={styles.image} src={cooking2} alt="cookech2.svg"/>
                        <img className={styles.pattern} src={pattern} alt="pattern.svg"/>
                    </div>
                </div>
            </div>
        </div>
    )
}
